const SideProject = () => {
    const horizontalRef = useRef(null);
    const sectionsRef = useRef([]);

    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
    
        const horizontal = horizontalRef.current;
        const sections = sectionsRef.current;

        let totalWidth = sections.reduce((acc, section)=> acc + section.offsetWidth, 0);
    
        let scrollTween = gsap.to(sections, {
            xPercent: -95 * (sections.length - 1),
            ease: "none",
            scrollTrigger: {
            trigger: horizontal,
            start: "top 56px",
            end: () => "+=" + (totalWidth - horizontal.offsetWidth),
            pin: true,
            scrub: 1,
            invalidateOnRefresh: true,
            anticipatePin: 1,
            },
        });
    
        return () => {
            scrollTween.kill();
        };
    }, []);

    return (
        <section id="Sideprojects" ref={horizontalRef}>
            <div className="sideproject__inner">
                <div className="sideproject__title">
                    Project Detail <em></em>
                </div>
                <div className="sideproject__wrap">
                    {sideprojectText.map((sideproject, key) => (
                        <article
                        className={`sideproject__item p${key + 1}`}
                        key={key}
                        ref={(el)=>(sectionsRef.current[key] =el)
                        }>
                            <span className="num">{sideproject.num}.</span>
                            {/* <a href={sideproject.code} target="_blank" className="img" rel="noreferrer">
                                <img src={sideproject.img} alt={sideproject.name} />
                            </a> */}
                            <h3 className="title">{sideproject.title}</h3>
                            <p className="desc">{sideproject.desc}</p>
                            <p className="period">{sideproject.period}</p>
                            <p className="skill">{sideproject.skill}</p>
                            
                            
                            <div class="buttonwrap">
                            <a href={sideproject.review} target="_blank" className="site" rel="noreferrer">Project Detail</a>
                            <a href={sideproject.view} target="_blank" className="site" rel="noreferrer">Overview PDF</a>
                            </div>
                        </article>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default SideProject;