import React, {useEffect} from "react";
import about from "../assets/img/about.jpg";


const introText = {
  title: "PROJECT PORTFOLIO",
  
  desc: ["JIN YOUNG SIN", "PORTFOLIO"],
};


const Intro = () => {

    //별 ~
    useEffect(() => {
        const stars = document.querySelectorAll('.star');
        stars.forEach(star => {
          const x = Math.random() * 100;  // 0에서 100 사이의 랜덤 값 (vh)
          const y = Math.random() * 100;  // 0에서 100 사이의 랜덤 값 (vh)
          star.style.top = `${y}vh`;
          star.style.left = `${x}vw`;
    
          // 애니메이션 지연 시간과 지속 시간을 무작위로 설정
          const delay = Math.random() * 6; // 최대 6초 지연
          const duration = Math.random() * 2 + 3; // 2초에서 6초 사이의 지속 시간
    
          star.style.animationDelay = `${delay}s`;
          star.style.animationDuration = `${duration}s`;
        });
      }, []);

      // 별똥별 ~~~
      // useEffect(() => {
      //   const starsContainer = document.querySelector(".stars");

      //   const createMeteor = () => {
      //     const meteor = document.createElement("div");
      //     meteor.className = "star meteor"; // 별똥별은 별 클래스 재활용

      //     // 별똥별 시작 위치 설정
      //     const startX = Math.random() * 80 + 10; // 10vw ~ 90vw
      //     const startY = Math.random() * 10; // 0vh ~ 10vh
      //     meteor.style.left = `${startX}vw`;
      //     meteor.style.top = `${startY}vh`;

      //     // `.stars` 컨테이너에 별똥별 추가
      //     starsContainer.appendChild(meteor);

      //     // 애니메이션이 끝난 후 DOM에서 제거
      //     setTimeout(() => {
      //       meteor.remove();
      //     }, 3000); // 3초 후 제거
      //   };

      //   const interval = setInterval(createMeteor, 2000); // 2초마다 별똥별 생성
      //   return () => clearInterval(interval); // 컴포넌트 언마운트 시 정리
      // }, []);


  return (
      <section id="intro">
          <div className="intro__inner">
          <div className="stars">
          {[...Array(200)].map((_, i) => (
            <div key={i} className="star"></div>
            ))}
            </div>
            <div className="intro__title__header">
              <h1 className="intro__title">{introText.title}</h1>
            </div>
              <div className="intro__text">
              <div className="stars">
          {[...Array(200)].map((_, i) => (
            <div key={i} className="star"></div>
            ))}
            </div>
                  <div className="text">
                      <div>{introText.desc[0]}</div>
                      <div>{introText.desc[1]}</div>
                      <div>{introText.desc[2]}</div>
                  </div>
                  <div className="img">
                      <img src={about} alt=" "/>
                  </div>
              </div>
              <div className="intro__lines bottom" aria-hidden="true">
                  <span className="line"></span>
                  <span className="line"></span>
                  <span className="line"></span>
                  <span className="line"></span>
                  <span className="line"></span>
                  <span className="line"></span>
                  <span className="line"></span>
              </div>
          </div>
      </section>
  );
};

export default Intro;
